import { surroundingAgent } from '../engine.mjs';
import {
  DefinePropertyOrThrow,
  OrdinaryFunctionCreate,
  OrdinaryObjectCreate,
  SetFunctionName,
  sourceTextMatchedBy,
} from '../abstract-ops/all.mjs';
import { X } from '../completion.mjs';
import { Descriptor, Value } from '../value.mjs';

// FAKE #sec-capture-operator-runtime-semantics-evaluation
//   CaptureExpression :
//     `&` `(` Expression `)`
//     `&` [lookahead ≠ `(`] CaptureCallExpression
export function Evaluate_CaptureExpression(CaptureExpression) {
  const { CaptureBody, CaptureParameters } = CaptureExpression;
  // 2. Let scope be the LexicalEnvironment of the running execution context.
  const scope = surroundingAgent.runningExecutionContext.LexicalEnvironment;
  // 3. Let privateScope be the running execution context's PrivateEnvironment.
  const privateScope = surroundingAgent.runningExecutionContext.PrivateEnvironment;
  // 4. Let sourceText be the source text matched by ArrowFunction.
  const sourceText = sourceTextMatchedBy(CaptureExpression);
  // 5. Let closure be OrdinaryFunctionCreate(%[Async][Generator]Function.prototype%, sourceText, CaptureParameters, CaptureBody, lexical-this, scope, privateScope).
  const tagAsync = CaptureBody.isAsync ? 'Async' : '';
  const tagGenerator = CaptureBody.isGenerator ? 'Generator' : '';
  const closure = OrdinaryFunctionCreate(
    surroundingAgent.intrinsic(`%${tagAsync}${tagGenerator}Function.prototype%`),
    sourceText,
    CaptureParameters,
    CaptureBody,
    'lexical-this',
    scope,
    privateScope,
  );
  // 6. Perform SetFunctionName(closure, "").
  SetFunctionName(closure, new Value(''));
  // 7. If CaptureBody Contains YieldExpression
  if (tagGenerator) {
    // a. Let prototype be OrdinaryObjectCreate(%[Async]GeneratorFunction.prototype.prototype%).
    const prototype = OrdinaryObjectCreate(surroundingAgent.intrinsic(`%${tagAsync}GeneratorFunction.prototype.prototype%`));
    // b. Perform ! DefinePropertyOrThrow(closure, "prototype", PropertyDescriptor { [[Value]]: prototype, [[Writable]]: true, [[Enumerable]]: false, [[Configurable]]: false }).
    X(DefinePropertyOrThrow(
      closure,
      new Value('prototype'),
      Descriptor({
        Value: prototype,
        Writable: Value.true,
        Enumerable: Value.false,
        Configurable: Value.false,
      }),
    ));
  }
  // 8. Return closure.
  return closure;
}
