import { surroundingAgent } from '../engine.mjs';
import { NewDeclarativeEnvironment } from '../environment.mjs';
import { Await, Q } from '../completion.mjs';
import { Evaluate } from '../evaluator.mjs';
import { Value } from '../value.mjs';
import { Call, GetValue, Yield } from '../abstract-ops/all.mjs';

// PipelineBody :
//   `await` PipelineBody
//   `yield` [no LineTerminator here] PipelineBody
//   LeftHandSideExpression
export function* Evaluate_PipelineBody(topicValue, PipelineBody) {
  switch (PipelineBody.type) {
    case 'AwaitExpression': {
      const expr = PipelineBody.UnaryExpression;
      const ref = yield* Evaluate_PipelineBody(topicValue, expr);
      const value = Q(GetValue(ref));
      return Q(yield* Await(value));
    }
    case 'YieldExpression': {
      const expr = PipelineBody.UnaryExpression;
      const ref = yield* Evaluate_PipelineBody(topicValue, expr);
      const value = Q(GetValue(ref));
      return Q(yield* Yield(value));
    }
    case 'AsyncArrowFunction':
    case 'AsyncFunctionExpression':
    case 'AsyncGeneratorExpression':
    case 'ArrowFunction':
    case 'FunctionExpression':
    case 'GeneratorExpression':
    case 'CaptureExpression': {
      const ref = yield* Evaluate(PipelineBody);
      const func = Q(GetValue(ref));
      return Q(Call(func, Value.undefined, [topicValue]));
    }
    case 'NewExpression':
    case 'CallExpression':
    case 'SuperCall': {
      const oldEnv = surroundingAgent.runningExecutionContext.LexicalEnvironment;
      const topicEnv = NewDeclarativeEnvironment(oldEnv);

      const TOPIC_NAME = new Value('&0');
      X(topicEnv.CreateImmutableBinding(TOPIC_NAME, Value.true));
      X(topicEnv.InitializeBinding(TOPIC_NAME, topicValue));

      surroundingAgent.runningExecutionContext.LexicalEnvironment = topicEnv;
      const R = yield* Evaluate(PipelineBody);
      surroundingAgent.runningExecutionContext.LexicalEnvironment = oldEnv;
      return R;
    }
    default:
      throw new OutOfRange('Evaluate_PipelineBody', PipelineBody);
  }
}

// FAKE #sec-pipeline-operator-runtime-semantics-evaluation
//   PipelineExpression :
//     UnaryExpression
//     PipelineExpression `|>` PipelineBody
//     PipelineExpression `?>` PipelineBody
export function* Evaluate_PipelineExpression({ left, operator, right }) {

  const topicReference = yield* Evaluate(left);

  const topicValue = Q(GetValue(topicReference));

  if (topicValue === Value.undefined || topicValue === Value.null) {
    if (operator === '?>') {
      return topicValue;
    }
  }

  return yield* Evaluate_PipelineBody(topicValue, right);
}
