const MaybeAssignTokens = [
  // Logical
  ['NULLISH', '??'],
  ['OR', '||'],
  ['AND', '&&'],

  // Pipeline
  ['PIPELINE', '|>'],
  ['OPTIONAL_PIPELINE', '?>'],

  // Binop
  ['BIT_OR', '|'],
  ['BIT_XOR', '^'],
  ['BIT_AND', '&'],
  ['SHL', '<<'],
  ['SAR', '>>'],
  ['SHR', '>>>'],
  ['MUL', '*'],
  ['DIV', '/'],
  ['MOD', '%'],
  ['EXP', '**'],

  // Unop
  ['ADD', '+'],
  ['SUB', '-'],
];

export const RawTokens = [
  // BEGIN PropertyOrCall
  // BEGIN Member
  // BEGIN Template
  ['TEMPLATE', '`'],
  // END Template

  // BEGIN Property
  ['PERIOD', '.'],
  ['LBRACK', '['],
  // END Property
  // END Member
  ['OPTIONAL', '?.'],
  ['LPAREN', '('],
  // END PropertyOrCall
  ['RPAREN', ')'],
  ['RBRACK', ']'],
  ['LBRACE', '{'],
  ['COLON', ':'],
  ['ELLIPSIS', '...'],
  ['CONDITIONAL', '?'],
  // BEGIN AutoSemicolon
  ['SEMICOLON', ';'],
  ['RBRACE', '}'],

  ['EOS', 'EOS'],
  // END AutoSemicolon

  // BEGIN ArrowOrAssign
  ['ARROW', '=>'],
  // BEGIN Assign
  ['ASSIGN', '='],
  ...MaybeAssignTokens.map(([n, v]) => [`ASSIGN_${n}`, `${v}=`]),
  // END Assign
  // END ArrowOrAssign

  // Binary operators by precidence
  ['COMMA', ','],

  ...MaybeAssignTokens,

  ['NOT', '!'],
  ['BIT_NOT', '~'],
  ['DELETE', 'delete'],
  ['TYPEOF', 'typeof'],
  ['VOID', 'void'],

  // BEGIN IsCountOp
  ['INC', '++'],
  ['DEC', '--'],
  // END IsCountOp
  // END IsUnaryOrCountOp

  ['EQ', '=='],
  ['EQ_STRICT', '==='],
  ['NE', '!='],
  ['NE_STRICT', '!=='],
  ['LT', '<'],
  ['GT', '>'],
  ['LTE', '<='],
  ['GTE', '>='],
  ['INSTANCEOF', 'instanceof'],
  ['IN', 'in'],

  ['BREAK', 'break'],
  ['CASE', 'case'],
  ['CATCH', 'catch'],
  ['CONTINUE', 'continue'],
  ['DEBUGGER', 'debugger'],
  ['DEFAULT', 'default'],
  // DELETE
  ['DO', 'do'],
  ['ELSE', 'else'],
  ['FINALLY', 'finally'],
  ['FOR', 'for'],
  ['FUNCTION', 'function'],
  ['IF', 'if'],
  // IN
  // INSTANCEOF
  ['NEW', 'new'],
  ['RETURN', 'return'],
  ['SWITCH', 'switch'],
  ['THROW', 'throw'],
  ['TRY', 'try'],
  // TYPEOF
  ['VAR', 'var'],
  // VOID
  ['WHILE', 'while'],
  ['WITH', 'with'],
  ['THIS', 'this'],

  ['NULL', 'null'],
  ['TRUE', 'true'],
  ['FALSE', 'false'],
  ['NUMBER', null],
  ['STRING', null],
  ['BIGINT', null],

  // BEGIN Callable
  ['SUPER', 'super'],
  // BEGIN AnyIdentifier
  ['IDENTIFIER', null],
  ['AWAIT', 'await'],
  ['YIELD', 'yield'],
  // END AnyIdentifier
  // END Callable
  ['CLASS', 'class'],
  ['CONST', 'const'],
  ['EXPORT', 'export'],
  ['EXTENDS', 'extends'],
  ['IMPORT', 'import'],
  ['PRIVATE_IDENTIFIER', null],
  ['CAPTURE_PLACEHOLDER', null],

  ['ENUM', 'enum'],

  ['ESCAPED_KEYWORD', null],
];

export const Token = RawTokens
  .reduce((obj, [name], i) => {
    obj[name] = i;
    return obj;
  }, Object.create(null));

export const TokenNames = RawTokens.map((r) => r[0]);

export const TokenValues = RawTokens.map((r) => r[1]);

const Keywords = RawTokens
  .filter(([name, raw]) => name.toLowerCase() === raw)
  .map(([, raw]) => raw);

export const KeywordLookup = Keywords
  .reduce((obj, kw) => {
    obj[kw] = Token[kw.toUpperCase()];
    return obj;
  }, Object.create(null));

const KeywordTokens = new Set(Object.values(KeywordLookup));

const isInRange = (t, l, h) => t >= l && t <= h;
export const isAutomaticSemicolon = (t) => isInRange(t, Token.SEMICOLON, Token.EOS);
export const isMember = (t) => isInRange(t, Token.TEMPLATE, Token.LBRACK);
export const isPropertyOrCall = (t) => isInRange(t, Token.TEMPLATE, Token.LPAREN);
export const isKeyword = (t) => KeywordTokens.has(t);
export const isKeywordRaw = (s) => Keywords.includes(s);

const ReservedWordsStrict = [
  'implements', 'interface', 'let',
  'package', 'private', 'protected',
  'public', 'static', 'yield',
];

export const isReservedWordStrict = (s) => ReservedWordsStrict.includes(s);
