import { surroundingAgent } from '../engine.mjs';
import {
  TV,
  PropName,
  StringValue,
  IsComputedPropertyKey,
  ContainsArguments,
} from '../static-semantics/all.mjs';
import {
  Token,
  isPropertyOrCall,
  isMember,
  isKeyword,
  isAutomaticSemicolon,
} from './tokens.mjs';
import { isLineTerminator } from './Lexer.mjs';
import { FunctionParser, FunctionKind } from './FunctionParser.mjs';
import { RegExpParser } from './RegExpParser.mjs';

export class ExpressionParser extends FunctionParser {
  // Expression :
  //   AssignmentExpression
  //   Expression `,` AssignmentExpression
  parseExpression() {
    const node = this.startNode();
    const AssignmentExpression = this.parseAssignmentExpression();
    if (this.eat(Token.COMMA)) {
      node.ExpressionList = [AssignmentExpression];
      do {
        node.ExpressionList.push(this.parseAssignmentExpression());
      } while (this.eat(Token.COMMA));
      return this.finishNode(node, 'CommaOperator');
    }
    return AssignmentExpression;
  }

  // AssignmentExpression :
  //   ConditionalExpression
  //   [+Yield] YieldExpression
  //   ArrowFunction
  //   AsyncArrowFunction
  //   LeftHandSideExpression `=` AssignmentExpression
  //   LeftHandSideExpression AssignmentOperator AssignmentExpression
  //   LeftHandSideExpression LogicalAssignmentOperator AssignmentExpression
  //
  // AssignmentOperator : one of
  //   *= /= %= += -= <<= >>= >>>= &= ^= |= **= |>= ?>=
  //
  // LogicalAssignmentOperator : one of
  //   &&= ||= ??=
  parseAssignmentExpression() {
    if (this.test(Token.YIELD) && this.scope.hasYield()) {
      return this.parseYieldExpression();
    }
    const node = this.startNode();

    this.scope.pushAssignmentInfo('assign');
    const left = this.parseConditionalExpression();
    const assignmentInfo = this.scope.popAssignmentInfo();

    if (left.type === 'IdentifierReference') {
      // `async` [no LineTerminator here] IdentifierReference [no LineTerminator here] `=>`
      if (left.name === 'async'
          && this.test(Token.IDENTIFIER)
          && !this.peek().hadLineTerminatorBefore
          && this.testAhead(Token.ARROW)
          && !this.peekAhead().hadLineTerminatorBefore) {
        assignmentInfo.clear();
        return this.parseArrowFunction(node, {
          Arguments: [this.parseIdentifierReference()],
        }, FunctionKind.ASYNC);
      }
      // IdentifierReference [no LineTerminator here] `=>`
      if (this.test(Token.ARROW) && !this.peek().hadLineTerminatorBefore) {
        assignmentInfo.clear();
        return this.parseArrowFunction(node, { Arguments: [left] }, FunctionKind.NORMAL);
      }
    }

    // `async` [no LineTerminator here] Arguments [no LineTerminator here] `=>`
    if (left.type === 'CallExpression' && left.arrowInfo && this.test(Token.ARROW)
        && !this.peek().hadLineTerminatorBefore) {
      const last = left.Arguments[left.Arguments.length - 1];
      if (!left.arrowInfo.trailingComma || (last && last.type !== 'AssignmentRestElement')) {
        assignmentInfo.clear();
        return this.parseArrowFunction(node, left, FunctionKind.ASYNC);
      }
    }

    if (left.type === 'CoverParenthesizedExpressionAndArrowParameterList') {
      assignmentInfo.clear();
      return this.parseArrowFunction(node, left, FunctionKind.NORMAL);
    }

    switch (this.peek().type) {
      case Token.ASSIGN:
      case Token.ASSIGN_MUL:
      case Token.ASSIGN_DIV:
      case Token.ASSIGN_MOD:
      case Token.ASSIGN_ADD:
      case Token.ASSIGN_SUB:
      case Token.ASSIGN_SHL:
      case Token.ASSIGN_SAR:
      case Token.ASSIGN_SHR:
      case Token.ASSIGN_BIT_AND:
      case Token.ASSIGN_BIT_XOR:
      case Token.ASSIGN_BIT_OR:
      case Token.ASSIGN_EXP:
      case Token.ASSIGN_AND:
      case Token.ASSIGN_OR:
      case Token.ASSIGN_NULLISH:
        assignmentInfo.clear();
        this.validateAssignmentTarget(left);
        node.LeftHandSideExpression = left;
        node.AssignmentOperator = this.next().value;
        node.AssignmentExpression = this.parseAssignmentExpression();
        return this.finishNode(node, 'AssignmentExpression');
      case Token.ASSIGN_PIPELINE:
      case Token.ASSIGN_OPTIONAL_PIPELINE:
        if (!this.feature('pipeline-operator')) {
          return this.unexpected();
        }
        assignmentInfo.clear();
        this.validateAssignmentTarget(left);
        node.LeftHandSideExpression = left;
        node.AssignmentOperator = this.next().value;
        node.AssignmentExpression = this.parsePipelineBody(left);
        return this.finishNode(node, 'AssignmentExpression');
      default:
        return left;
    }
  }

  validateAssignmentTarget(node) {
    switch (node.type) {
      case 'IdentifierReference':
        if (this.isStrictMode() && (node.name === 'eval' || node.name === 'arguments')) {
          break;
        }
        return;
      case 'CoverInitializedName':
        this.validateAssignmentTarget(node.IdentifierReference);
        return;
      case 'MemberExpression':
        return;
      case 'SuperProperty':
        return;
      case 'ParenthesizedExpression':
        if (node.Expression.type === 'ObjectLiteral' || node.Expression.type === 'ArrayLiteral') {
          break;
        }
        this.validateAssignmentTarget(node.Expression);
        return;
      case 'ArrayLiteral':
        node.ElementList.forEach((p, i) => {
          if (p.type === 'SpreadElement' && (i !== node.ElementList.length - 1 || node.hasTrailingComma)) {
            this.raiseEarly('InvalidAssignmentTarget', p);
          }
          if (p.type === 'AssignmentExpression') {
            this.validateAssignmentTarget(p.LeftHandSideExpression);
          } else {
            this.validateAssignmentTarget(p);
          }
        });
        return;
      case 'ObjectLiteral':
        node.PropertyDefinitionList.forEach((p, i) => {
          if (p.type === 'PropertyDefinition' && !p.PropertyName
              && i !== node.PropertyDefinitionList.length - 1) {
            this.raiseEarly('InvalidAssignmentTarget', p);
          }
          this.validateAssignmentTarget(p);
        });
        return;
      case 'PropertyDefinition':
        if (node.AssignmentExpression.type === 'AssignmentExpression') {
          this.validateAssignmentTarget(node.AssignmentExpression.LeftHandSideExpression);
        } else {
          this.validateAssignmentTarget(node.AssignmentExpression);
        }
        return;
      case 'Elision':
        return;
      case 'SpreadElement':
        if (node.AssignmentExpression.type === 'AssignmentExpression') {
          break;
        }
        this.validateAssignmentTarget(node.AssignmentExpression);
        return;
      default:
        break;
    }
    this.raiseEarly('InvalidAssignmentTarget', node);
  }

  // YieldExpression :
  //   `yield`
  //   `yield` [no LineTerminator here] AssignmentExpression
  //   `yield` [no LineTerminator here] `*` AssignmentExpression
  parseYieldExpression(topic) {
    if (this.scope.inParameters()) {
      this.raiseEarly('YieldInFormalParameters');
    }
    const node = this.startNode();
    let hasArgument;
    this.expect(Token.YIELD);
    if (this.peek().hadLineTerminatorBefore) {
      node.hasStar = hasArgument = false;
    } else if (this.eat(Token.MUL)) {
      node.hasStar = hasArgument = true;
    } else {
      node.hasStar = false;
      switch (this.peek().type) {
        case Token.EOS:
        case Token.SEMICOLON:
        case Token.RBRACE:
        case Token.RBRACK:
        case Token.RPAREN:
        case Token.COLON:
        case Token.COMMA:
        case Token.IN:
          hasArgument = false;
          break;
        default:
          hasArgument = true;
      }
    }
    if (!hasArgument) {
      if (topic) {
        this.raiseEarly('YieldWithoutArgumentInPipeline');
      }
      node.AssignmentExpression = null;
    } else if (topic) {
      node.AssignmentExpression = this.parsePipelineBody(topic);
    } else {
      node.AssignmentExpression = this.parseAssignmentExpression();
    }
    this.scope.arrowInfo?.yieldExpressions.push(node);
    return this.finishNode(node, 'YieldExpression');
  }

  // ConditionalExpression :
  //   ShortCircuitExpression
  //   ShortCircuitExpression `?` AssignmentExpression `:` AssignmentExpression
  parseConditionalExpression() {
    const node = this.startNode();
    const ShortCircuitExpression = this.parseShortCircuitExpression();
    if (this.eat(Token.CONDITIONAL)) {
      node.ShortCircuitExpression = ShortCircuitExpression;
      this.scope.with({ in: true }, () => {
        node.AssignmentExpression_a = this.parseAssignmentExpression();
      });
      this.expect(Token.COLON);
      node.AssignmentExpression_b = this.parseAssignmentExpression();
      return this.finishNode(node, 'ConditionalExpression');
    }
    return ShortCircuitExpression;
  }

  // ShortCircuitExpression :
  //   LogicalORExpression
  //   CoalesceExpression
  //
  // CoalesceExpression :
  //   CoalesceExpressionHead `??` BitwiseORExpression
  //
  // CoalesceExpressionHead :
  //   CoalesceExpression
  //   BitwiseORExpression
  parseShortCircuitExpression() {
    let result = this.parseLogicalORExpression();
    while (this.test(Token.NULLISH)) {
      const { type } = result;
      if (type === 'LogicalANDExpression' || type === 'LogicalORExpression') {
        this.raise(
          'MixingLogicalOperators', this.peek(),
          result.operator, this.peek().value,
        );
      }
      const node = this.startNode(result);
      node.CoalesceExpressionHead = result;
      node.operator = this.next().value;
      node.BitwiseORExpression = this.parseBitwiseORExpression();
      result = this.finishNode(node, 'CoalesceExpression');
    }
    return result;
  }

  // parse a sequence of same-precedence left-associative binary operators
  parseBinaryChain(parseArg, nodeType, leftKey, rightKey, ...operators) {
    let result = this[parseArg]();
    while (operators.includes(this.peek().type)) {
      const node = this.startNode(result);
      node[leftKey] = result;
      node.operator = this.next().value;
      node[rightKey] = this[parseArg]();
      result = this.finishNode(node, nodeType);
    }
    return result;
  }

  // LogicalORExpression :
  //   LogicalANDExpression
  //   LogicalORExpression `||` LogicalANDExpression
  parseLogicalORExpression() {
    return this.parseBinaryChain(
      'parseLogicalANDExpression',
      'LogicalORExpression',
      'LogicalORExpression', 'LogicalANDExpression',
      Token.OR,
    );
  }

  // LogicalANDExpression :
  //   BitwiseORExpression
  //   LogicalANDExpression `&&` BitwiseORExpression
  parseLogicalANDExpression() {
    return this.parseBinaryChain(
      'parseBitwiseORExpression',
      'LogicalANDExpression',
      'LogicalANDExpression', 'BitwiseORExpression',
      Token.AND,
    );
  }

  // BitwiseORExpression :
  //   BitwiseXORExpression
  //   BitwiseORExpression `|` BitwiseXORExpression
  parseBitwiseORExpression() {
    return this.parseBinaryChain(
      'parseBitwiseXORExpression',
      'BitwiseORExpression',
      'A', 'B',
      Token.BIT_OR,
    );
  }

  // BitwiseXORExpression :
  //   BitwiseANDExpression
  //   BitwiseXORExpression `^` BitwiseANDExpression
  parseBitwiseXORExpression() {
    return this.parseBinaryChain(
      'parseBitwiseANDExpression',
      'BitwiseXORExpression',
      'A', 'B',
      Token.BIT_XOR,
    );
  }

  // BitwiseANDExpression :
  //   EqualityExpression
  //   BitwiseANDExpression `&` EqualityExpression
  parseBitwiseANDExpression() {
    return this.parseBinaryChain(
      'parseEqualityExpression',
      'BitwiseANDExpression',
      'A', 'B',
      Token.BIT_AND,
    );
  }

  // EqualityExpression :
  //   RelationalExpression
  //   EqualityExpression `==` RelationalExpression
  //   EqualityExpression `!=` RelationalExpression
  //   EqualityExpression `===` RelationalExpression
  //   EqualityExpression `!==` RelationalExpression
  parseEqualityExpression() {
    return this.parseBinaryChain(
      'parseRelationalExpression',
      'EqualityExpression',
      'EqualityExpression', 'RelationalExpression',
      Token.EQ,
      Token.NE,
      Token.EQ_STRICT,
      Token.NE_STRICT,
    );
  }

  // RelationalExpression :
  //   ShiftExpression
  //   RelationalExpression `<` ShiftExpression
  //   RelationalExpression `>` ShiftExpression
  //   RelationalExpression `<=` ShiftExpression
  //   RelationalExpression `>=` ShiftExpression
  //   RelationalExpression `instanceof` ShiftExpression
  //   [+In] RelationalExpression `in` ShiftExpression
  //   [+In] PrivateIdentifier `in` ShiftExpression
  parseRelationalExpression() {
    const hasIn = this.scope.hasIn();
    if (hasIn && this.test(Token.PRIVATE_IDENTIFIER)) {
      const left = this.parsePrivateIdentifier();
      this.scope.checkUndefinedPrivate(left);
      const result = this.startNode(left);
      result.PrivateIdentifier = left;
      result.operator = this.expect(Token.IN).value;
      result.ShiftExpression = this.parseShiftExpression();
      return this.finishNode(result, 'RelationalExpression');
    }
    return this.parseBinaryChain(
      'parseShiftExpression',
      'RelationalExpression',
      'RelationalExpression', 'ShiftExpression',
      Token.LT,
      Token.GT,
      Token.LTE,
      Token.GTE,
      Token.INSTANCEOF,
      hasIn ? Token.IN : -1,
    );
  }

  // ShiftExpression :
  //   AdditiveExpression
  //   ShiftExpression `<<` AdditiveExpression
  //   ShiftExpression `>>` AdditiveExpression
  //   ShiftExpression `>>>` AdditiveExpression
  parseShiftExpression() {
    return this.parseBinaryChain(
      'parseAdditiveExpression',
      'ShiftExpression',
      'ShiftExpression', 'AdditiveExpression',
      Token.SHL,
      Token.SAR,
      Token.SHR,
    );
  }

  // AdditiveExpression :
  //   MultiplicativeExpression
  //   AdditiveExpression `+` MultiplicativeExpression
  //   AdditiveExpression `-` MultiplicativeExpression
  parseAdditiveExpression() {
    return this.parseBinaryChain(
      'parseMultiplicativeExpression',
      'AdditiveExpression',
      'AdditiveExpression', 'MultiplicativeExpression',
      Token.ADD,
      Token.SUB,
    );
  }

  // MultiplicativeExpression :
  //   ExponentiationExpression
  //   MultiplicativeExpression MultiplicativeOperator ExponentiationExpression
  //
  // MultiplicativeOperator : one of
  //   * / %
  parseMultiplicativeExpression() {
    return this.parseBinaryChain(
      'parseExponentiationExpression',
      'MultiplicativeExpression',
      'MultiplicativeExpression', 'ExponentiationExpression',
      Token.MUL,
      Token.DIV,
      Token.MOD,
    );
  }

  // ExponentiationExpression :
  //   PipelineExpression
  //   UpdateExpression `**` ExponentiationExpression
  parseExponentiationExpression() {
    const expressions = [];
    do {
      const pipeline = this.parsePipelineExpression();
      const { type } = pipeline;
      expressions.push(pipeline);
      if (type === 'PipelineExpression'
        || type === 'UnaryExpression'
        || type === 'AwaitExpression') {
        // not an UpdateExpression
        break;
      }
    } while (this.eat(Token.EXP));
    // exponentiation is right-associative
    return expressions.reduceRight((right, left) => {
      const node = this.startNode(left);
      node.UpdateExpression = left;
      node.operator = '**';
      node.ExponentiationExpression = right;
      return this.finishNode(node, 'ExponentiationExpression');
    });
  }

  // PipelineExpression :
  //   UnaryExpression
  //   PipelineExpression `|>` PipelineBody
  //   PipelineExpression `?>` PipelineBody
  parsePipelineExpression() {
    let result = this.parseUnaryExpression();

    while (true) {
      switch (this.peek().type) {
        case Token.PIPELINE:
        case Token.OPTIONAL_PIPELINE: {
          if (!this.feature('pipeline-operator')) {
            return this.unexpected();
          }
          const node = this.startNode(result);
          node.left = result;
          node.operator = this.next().value;
          node.right = this.parsePipelineBody(result);
          result = this.finishNode(node, 'PipelineExpression');
          continue;
        }
        default:
          return result;
      }
    }
  }

  // PipelineBody :
  //   `await` PipelineBody
  //   `yield` [no LineTerminator here] PipelineBody
  //   LeftHandSideExpression
  //
  // PipelineCallExpression : (refines LeftHandSideExpression)
  //   NewExpression OptionalArguments
  //   CallExpression OptionalArguments
  //   SuperCall
  //   ImportCall
  //   OptionalExpression OptionalArguments
  //   ArrowFunction
  //   AsyncArrowFunction
  //   CaptureExpression
  //
  // OptionalArguments :
  //   Arguments
  //   `?.` Arguments
  parsePipelineBody(topic) {
    if (this.test(Token.AWAIT) && this.scope.hasAwait()) {
      return this.parseAwaitExpression(topic);
    }
    if (this.test(Token.YIELD) && this.scope.hasYield()) {
      return this.parseYieldExpression(topic);
    }

    const rhs = this.parseLeftHandSideExpression();
    let target = rhs;

    while (target.type === 'ParenthesizedExpression') {
      target = target.Expression;
    }

    switch (target.type) {
      case 'AsyncArrowFunction':
      case 'AsyncFunctionExpression':
      case 'AsyncGeneratorExpression':
      case 'ArrowFunction':
      case 'FunctionExpression':
      case 'GeneratorExpression':
      case 'CaptureExpression':
        break;
      case 'NewExpression':
      case 'CallExpression':
      case 'SuperCall':
        if (target.Arguments == null) {
          this.raiseEarly('InvalidPipelineBody', target);
        } else {
          const topicRef = this.startNode(topic);
          topicRef.location.endIndex = topic.location.endIndex;
          topicRef.location.end.line = topic.location.end.line;
          topicRef.location.end.column = topic.location.end.column;
          topicRef.name = '&0';
          topicRef.type = 'IdentifierReference';
          target.Arguments.unshift(topicRef);
        }
        break;
      case 'ImportCall':
        // FIXME this won't work because ImportCall only
        //      takes AssignmentExpression, not Arguments
        // fall-through
      default:
        this.raiseEarly('InvalidPipelineBody', target);
        break;
    }
    return target;
  }

  // UnaryExpression :
  //   UpdateExpression
  //   `delete` UnaryExpression
  //   `void` UnaryExpression
  //   `typeof` UnaryExpression
  //   `+` UnaryExpression
  //   `-` UnaryExpression
  //   `~` UnaryExpression
  //   `!` UnaryExpression
  //   [+Await] AwaitExpression
  parseUnaryExpression() {
    return this.scope.with({ in: true }, () => {
      if (this.test(Token.AWAIT) && this.scope.hasAwait()) {
        return this.parseAwaitExpression();
      }
      const node = this.startNode();
      switch (this.peek().type) {
        case Token.DELETE:
        case Token.VOID:
        case Token.TYPEOF:
        case Token.ADD:
        case Token.SUB:
        case Token.BIT_NOT:
        case Token.NOT:
          node.operator = this.next().value;
          node.UnaryExpression = this.parseUnaryExpression();
          if (node.operator === 'delete') {
            let target = node.UnaryExpression;
            while (target.type === 'ParenthesizedExpression') {
              target = target.Expression;
            }
            if (this.isStrictMode() && target.type === 'IdentifierReference') {
              this.raiseEarly('DeleteIdentifier', target);
            }
            if (target.type === 'MemberExpression' && target.PrivateIdentifier) {
              this.raiseEarly('DeletePrivateName', target);
            }
          }
          return this.finishNode(node, 'UnaryExpression');
        default:
          return this.parseUpdateExpression();
      }
    });
  }

  // AwaitExpression :
  //   [~Pipeline] `await` UnaryExpression
  //   [+Pipeline] `await` PipelineBody
  parseAwaitExpression(topic) {
    if (this.scope.inParameters()) {
      this.raiseEarly('AwaitInFormalParameters');
    }
    const node = this.startNode();
    this.expect(Token.AWAIT);
    if (topic) {
      node.UnaryExpression = this.parsePipelineBody(topic);
    } else {
      node.UnaryExpression = this.parseUnaryExpression();
    }
    this.scope.arrowInfo?.awaitExpressions.push(node);
    if (!this.scope.hasReturn()) {
      this.state.hasTopLevelAwait = true;
    }
    return this.finishNode(node, 'AwaitExpression');
  }

  // UpdateExpression :
  //   LeftHandSideExpression
  //   LeftHandSideExpression [no LineTerminator here] `++`
  //   LeftHandSideExpression [no LineTerminator here] `--`
  //   `++` UnaryExpression
  //   `--` UnaryExpression
  parseUpdateExpression() {
    if (this.test(Token.INC) || this.test(Token.DEC)) {
      const node = this.startNode();
      node.operator = this.next().value;
      node.LeftHandSideExpression = null;
      node.UnaryExpression = this.parseUnaryExpression();
      this.validateAssignmentTarget(node.UnaryExpression);
      return this.finishNode(node, 'UpdateExpression');
    }
    const argument = this.parseLeftHandSideExpression();
    if (!this.peek().hadLineTerminatorBefore) {
      if (this.test(Token.INC) || this.test(Token.DEC)) {
        this.validateAssignmentTarget(argument);
        const node = this.startNode();
        node.operator = this.next().value;
        node.LeftHandSideExpression = argument;
        node.UnaryExpression = null;
        return this.finishNode(node, 'UpdateExpression');
      }
    }
    return argument;
  }

  // CaptureExpression :
  //   `&` `(` Expression `)`
  //   `&` [lookahead ≠ `(`] CaptureCallExpression
  parseCaptureExpression() {
    const result = this.startNode();
    result.operator = this.expect(Token.BIT_AND).value;

    const conciseBody = this.startNode();
    const exprBody = this.startNode();
    const placeholders = [];
    const captureInfo = { placeholders, captured: false };

    this.scope.pushCaptureInfo(captureInfo);
    this.scope.pushArrowInfo(true);

    this.scope.with({
      default: false,
      return: true,
      await: true,
      yield: true,
      parameters: false,
      capturePlaceholders: false,
    }, () => {
      let inner = this.startNode();

      if (this.eat(Token.LPAREN)) {
        this.scope.withCapture(captureInfo, () => {
          inner.Expression = this.parseExpression();
        });
        this.expect(Token.RPAREN);
        this.finishNode(inner, 'ParenthesizedExpression');
      } else {
        inner = this.parseCaptureCallExpression(captureInfo);
      }

      if (!captureInfo.captured) {
        this.raiseEarly('CaptureWithoutCall');
      }

      exprBody.AssignmentExpression = inner;
    });

    const arrowInfo = this.scope.popArrowInfo();
    this.scope.popCaptureInfo();

    conciseBody.ExpressionBody = this.finishNode(exprBody, 'ExpressionBody');
    conciseBody.isAsync = arrowInfo.awaitExpressions.length > 0;
    conciseBody.isGenerator = arrowInfo.yieldExpressions.length > 0;
    result.CaptureBody = this.finishNode(conciseBody, 'CaptureBody');
    result.CaptureParameters = [];

    this.scope.with({ parameters: true }, () => {
      for (let index = 1; index < placeholders.length; ++index) {
        const placeholder = this.startNode();
        placeholder.name = `&${index}`;
        this.finishNode(placeholder, 'BindingIdentifier');
        const container = this.startNode();
        container.BindingIdentifier = placeholder;
        container.Initializer = null;
        this.scope.declare(placeholder, 'parameter');
        this.finishNode(container, 'SingleNameBinding');
        result.CaptureParameters.push(container);
      }
    });

    return this.finishNode(result, 'CaptureExpression');
  }

  // CaptureCallExpression :
  //   SuperCall
  //   ImportCall
  //   new CaptureMemberExpression Arguments
  //   CaptureMemberExpression Arguments
  //   CaptureMemberExpression TemplateLiteral
  //
  // CaptureMemberExpression :
  //   PrimaryExpression
  //   SuperProperty
  //   MetaProperty
  //   CaptureMemberExpression MemberAccess
  //   CaptureMemberExpression OptionalMemberAccess
  //
  // MemberAccess :
  //   [ Expression ]
  //   . IdentifierName
  //   . PrivateIdentifier
  //
  // OptionalMemberAccess :
  //   ?. [ Expression ]
  //   ?. IdentifierName
  //   ?. PrivateIdentifier
  parseCaptureCallExpression(captureInfo) {
    return this.parseLeftHandSideExpression(true, captureInfo);
  }

  // CapturePlaceholder :
  //   `&` DecimalDigits[~Sep]
  maybeParseCapturePlaceholder() {
    const node = this.startNode();
    const token = this.eatCapturePlaceholder();
    if (!token) {
      return null;
    }
    if (!this.scope.has('capturePlaceholders')) {
      return this.unexpected(token);
    }
    const { placeholders } = this.scope.captureInfo;
    const index = token.value;
    node.name = placeholders[index] = `&${index}`;
    return this.finishNode(node, 'IdentifierReference');
  }

  // LeftHandSideExpression :
  //   NewExpression
  //   CallExpression
  //   OptionalExpression
  parseLeftHandSideExpression(allowCalls = true, captureInfo) {
    let result = this.parseLeftHandSideExpressionHead(allowCalls, captureInfo);
    const check = allowCalls ? isPropertyOrCall : isMember;
    while (!captureInfo?.captured && check(this.peek().type)) {
      result = this.parseLeftHandSideExpressionTail(result, captureInfo);
    }
    return result;
  }

  // LeftHandSideExpression
  parseLeftHandSideExpressionHead(allowCalls = true, captureInfo) {
    let result;
    switch (this.peek().type) {
      case Token.NEW:
        result = this.parseNewExpression(captureInfo);
        break;
      case Token.SUPER: {
        const node = this.startNode();
        this.next();
        if (this.test(Token.LPAREN)) {
          if (!this.scope.hasSuperCall()) {
            this.raiseEarly('InvalidSuperCall');
          }
          node.Arguments = this.parseArguments(captureInfo).Arguments;
          result = this.finishNode(node, 'SuperCall');
        } else {
          if (!this.scope.hasSuperProperty()) {
            this.raiseEarly('InvalidSuperProperty');
          }
          if (this.eat(Token.LBRACK)) {
            node.Expression = this.parseExpression();
            this.expect(Token.RBRACK);
            node.IdentifierName = null;
          } else {
            this.expect(Token.PERIOD);
            node.Expression = null;
            node.IdentifierName = this.parseIdentifierName();
          }
          result = this.finishNode(node, 'SuperProperty');
        }
        break;
      }
      case Token.IMPORT: {
        const node = this.startNode();
        this.next();
        if (this.scope.hasImportMeta() && this.eat(Token.PERIOD)) {
          this.expect('meta');
          result = this.finishNode(node, 'ImportMeta');
        } else {
          if (!allowCalls) {
            this.unexpected();
          }
          this.expect(Token.LPAREN);
          this.scope.withCapture(captureInfo, () => {
            node.AssignmentExpression = this.parseAssignmentExpression();
          });
          this.expect(Token.RPAREN);
          result = this.finishNode(node, 'ImportCall');
        }
        break;
      }
      default:
        result = this.parsePrimaryExpression(captureInfo);
        break;
    }
    return result;
  }

  parseMemberAccess(object, captureInfo) {
    const node = this.startNode(object);

    if (this.eat(Token.LBRACK)) {
      node.MemberExpression = object;
      node.IdentifierName = null;
      node.Expression = this.parseExpression();
      this.expect(Token.RBRACK);
      return this.finishNode(node, 'MemberExpression');
    }

    if (this.eat(Token.PERIOD)) {
      node.MemberExpression = object;
      if (this.test(Token.PRIVATE_IDENTIFIER)) {
        node.PrivateIdentifier = this.parsePrivateIdentifier();
        this.scope.checkUndefinedPrivate(node.PrivateIdentifier);
        node.IdentifierName = null;
      } else {
        node.IdentifierName = this.parseIdentifierName();
        node.PrivateIdentifier = null;
      }
      node.Expression = null;
      return this.finishNode(node, 'MemberExpression');
    }

    node.MemberExpression = object;
    node.OptionalChain = this.parseOptionalChain(captureInfo);
    return this.finishNode(node, 'OptionalExpression');
  }

  // LeftHandSideExpression
  parseLeftHandSideExpressionTail(result, captureInfo) {
    const node = this.startNode(result);
    switch (this.peek().type) {
      case Token.LBRACK:
      case Token.PERIOD:
      case Token.OPTIONAL:
        result = this.parseMemberAccess(result, captureInfo);
        break;
      case Token.LPAREN: {
        // `async` [no LineTerminator here] `(`
        const couldBeArrow = !captureInfo
          && result.type === 'IdentifierReference'
          && this.matches('async', this.currentToken)
          && !this.peek().hadLineTerminatorBefore;
        if (couldBeArrow) {
          this.scope.pushArrowInfo(true);
        }
        const { Arguments, trailingComma } = this.parseArguments(captureInfo);
        node.CallExpression = result;
        node.Arguments = Arguments;
        if (couldBeArrow) {
          node.arrowInfo = this.scope.popArrowInfo();
          node.arrowInfo.trailingComma = trailingComma;
        }
        result = this.finishNode(node, 'CallExpression');
        break;
      }
      case Token.TEMPLATE:
        node.MemberExpression = result;
        this.scope.withCapture(captureInfo, () => {
          node.TemplateLiteral = this.parseTemplateLiteral(true);
        });
        result = this.finishNode(node, 'TaggedTemplateExpression');
        break;
      default:
        this.unexpected();
    }
    return result;
  }

  // OptionalChain
  parseOptionalChain(captureInfo) {
    this.expect(Token.OPTIONAL);
    let base = this.startNode();
    base.OptionalChain = null;
    if (this.test(Token.LPAREN)) {
      base.Arguments = this.parseArguments(captureInfo).Arguments;
    } else if (this.eat(Token.LBRACK)) {
      base.Expression = this.parseExpression();
      this.expect(Token.RBRACK);
    } else if (this.test(Token.TEMPLATE)) {
      this.raise('TemplateInOptionalChain');
    } else if (this.test(Token.PRIVATE_IDENTIFIER)) {
      base.PrivateIdentifier = this.parsePrivateIdentifier();
      this.scope.checkUndefinedPrivate(base.PrivateIdentifier);
    } else {
      base.IdentifierName = this.parseIdentifierName();
    }
    base = this.finishNode(base, 'OptionalChain');

    while (!captureInfo?.captured) {
      const node = this.startNode();
      if (this.test(Token.LPAREN)) {
        node.OptionalChain = base;
        node.Arguments = this.parseArguments(captureInfo).Arguments;
        base = this.finishNode(node, 'OptionalChain');
      } else if (this.eat(Token.LBRACK)) {
        node.OptionalChain = base;
        node.Expression = this.parseExpression();
        this.expect(Token.RBRACK);
        base = this.finishNode(node, 'OptionalChain');
      } else if (this.test(Token.TEMPLATE)) {
        this.raise('TemplateInOptionalChain');
      } else if (this.eat(Token.PERIOD)) {
        node.OptionalChain = base;
        if (this.test(Token.PRIVATE_IDENTIFIER)) {
          node.PrivateIdentifier = this.parsePrivateIdentifier();
          this.scope.checkUndefinedPrivate(node.PrivateIdentifier);
        } else {
          node.IdentifierName = this.parseIdentifierName();
        }
        base = this.finishNode(node, 'OptionalChain');
      } else {
        return base;
      }
    }

    return base;
  }

  // NewExpression
  parseNewExpression(captureInfo) {
    const node = this.startNode();
    this.expect(Token.NEW);
    if (this.scope.hasNewTarget() && this.eat(Token.PERIOD)) {
      this.expect('target');
      return this.finishNode(node, 'NewTarget');
    }
    node.MemberExpression = this.parseLeftHandSideExpression(false);
    if (this.test(Token.LPAREN)) {
      node.Arguments = this.parseArguments(captureInfo).Arguments;
    } else {
      if (captureInfo) {
        this.raiseEarly('CaptureWithoutCall');
      }
      node.Arguments = null;
    }
    return this.finishNode(node, 'NewExpression');
  }

  // PrimaryExpression :
  //   ...
  parsePrimaryExpression(captureInfo) {
    switch (this.peek().type) {
      case Token.IDENTIFIER:
      case Token.ESCAPED_KEYWORD:
      case Token.YIELD:
      case Token.AWAIT:
        // `async` [no LineTerminator here] `function`
        if (this.test('async') && this.testAhead(Token.FUNCTION)
            && !this.peekAhead().hadLineTerminatorBefore) {
          return this.parseFunctionExpression(FunctionKind.ASYNC);
        }
        return this.parseIdentifierReference();
      case Token.THIS: {
        const node = this.startNode();
        this.next();
        return this.finishNode(node, 'ThisExpression');
      }
      case Token.VAR: {
        if (!surroundingAgent.feature('var-expression')) {
          return this.unexpected();
        }
        const node = this.startNode();
        this.next();
        return this.finishNode(node, 'VarExpression');
      }
      case Token.NUMBER:
      case Token.BIGINT:
        return this.parseNumericLiteral();
      case Token.STRING:
        return this.parseStringLiteral();
      case Token.NULL: {
        const node = this.startNode();
        this.next();
        return this.finishNode(node, 'NullLiteral');
      }
      case Token.TRUE:
      case Token.FALSE:
        return this.parseBooleanLiteral();
      case Token.LBRACK:
        return this.parseArrayLiteral();
      case Token.LBRACE:
        return this.parseObjectLiteral();
      case Token.FUNCTION:
        return this.parseFunctionExpression(FunctionKind.NORMAL);
      case Token.CLASS:
        return this.parseClassExpression();
      case Token.TEMPLATE:
        return this.scope.withCapture(captureInfo, () => {
          return this.parseTemplateLiteral();
        });
      case Token.DIV:
      case Token.ASSIGN_DIV:
        return this.parseRegularExpressionLiteral();
      case Token.BIT_AND:
        if (this.feature('capture-operator')) {
          const placeholder = this.maybeParseCapturePlaceholder();
          if (placeholder) {
            return placeholder;
          }
          if (!captureInfo) {
            return this.parseCaptureExpression();
          }
        }
        return this.unexpected();
      case Token.LPAREN:
        return this.parseCoverParenthesizedExpressionAndArrowParameterList();
      default:
        return this.unexpected();
    }
  }

  // NumericLiteral
  parseNumericLiteral() {
    const node = this.startNode();
    if (!this.test(Token.NUMBER) && !this.test(Token.BIGINT)) {
      this.unexpected();
    }
    node.value = this.next().value;
    return this.finishNode(node, 'NumericLiteral');
  }

  // StringLiteral
  parseStringLiteral() {
    const node = this.startNode();
    if (!this.test(Token.STRING)) {
      this.unexpected();
    }
    node.value = this.next().value;
    return this.finishNode(node, 'StringLiteral');
  }

  // BooleanLiteral :
  //   `true`
  //   `false`
  parseBooleanLiteral() {
    const node = this.startNode();
    switch (this.peek().type) {
      case Token.TRUE:
        this.next();
        node.value = true;
        break;
      case Token.FALSE:
        this.next();
        node.value = false;
        break;
      default:
        this.unexpected();
    }
    return this.finishNode(node, 'BooleanLiteral');
  }

  // ArrayLiteral :
  //   `[` `]`
  //   `[` Elision `]`
  //   `[` ElementList `]`
  //   `[` ElementList `,` `]`
  //   `[` ElementList `,` Elision `]`
  parseArrayLiteral() {
    const node = this.startNode();
    this.expect(Token.LBRACK);
    node.ElementList = [];
    node.hasTrailingComma = false;
    while (true) {
      while (this.test(Token.COMMA)) {
        const elision = this.startNode();
        this.next();
        node.ElementList.push(this.finishNode(elision, 'Elision'));
      }
      if (this.eat(Token.RBRACK)) {
        break;
      }
      if (this.test(Token.ELLIPSIS)) {
        const spread = this.startNode();
        this.next();
        spread.AssignmentExpression = this.parseAssignmentExpression();
        node.ElementList.push(this.finishNode(spread, 'SpreadElement'));
      } else {
        node.ElementList.push(this.parseAssignmentExpression());
      }
      if (this.eat(Token.RBRACK)) {
        node.hasTrailingComma = false;
        break;
      }
      node.hasTrailingComma = true;
      this.expect(Token.COMMA);
    }
    return this.finishNode(node, 'ArrayLiteral');
  }

  // ObjectLiteral :
  //   `{` `}`
  //   `{` PropertyDefinitionList `}`
  //   `{` PropertyDefinitionList `,` `}`
  parseObjectLiteral() {
    const node = this.startNode();
    this.expect(Token.LBRACE);
    node.PropertyDefinitionList = [];
    let hasProto = false;
    while (true) {
      if (this.eat(Token.RBRACE)) {
        break;
      }
      const PropertyDefinition = this.parsePropertyDefinition();
      if (!this.state.json
          && PropertyDefinition.type === 'PropertyDefinition'
          && PropertyDefinition.PropertyName
          && !IsComputedPropertyKey(PropertyDefinition.PropertyName)
          && PropertyDefinition.PropertyName.type !== 'NumericLiteral'
          && StringValue(PropertyDefinition.PropertyName).stringValue() === '__proto__') {
        if (hasProto) {
          this.scope.registerObjectLiteralEarlyError(this.raiseEarly('DuplicateProto', PropertyDefinition.PropertyName));
        } else {
          hasProto = true;
        }
      }
      node.PropertyDefinitionList.push(PropertyDefinition);
      if (this.eat(Token.RBRACE)) {
        break;
      }
      this.expect(Token.COMMA);
    }
    return this.finishNode(node, 'ObjectLiteral');
  }

  parsePropertyDefinition() {
    return this.parseBracketedDefinition('property');
  }

  parseFunctionExpression(kind) {
    return this.parseFunction(true, kind);
  }

  parseArguments(captureInfo) {
    if (captureInfo) {
      return this.scope.withCapture(captureInfo, () => this.parseArguments());
    }
    this.expect(Token.LPAREN);
    if (this.eat(Token.RPAREN)) {
      return { Arguments: [], trailingComma: false };
    }
    const Arguments = [];
    let trailingComma = false;
    while (true) {
      const node = this.startNode();
      if (this.eat(Token.ELLIPSIS)) {
        node.AssignmentExpression = this.parseAssignmentExpression();
        Arguments.push(this.finishNode(node, 'AssignmentRestElement'));
      } else {
        Arguments.push(this.parseAssignmentExpression());
      }
      if (this.eat(Token.RPAREN)) {
        break;
      }
      this.expect(Token.COMMA);
      if (this.eat(Token.RPAREN)) {
        trailingComma = true;
        break;
      }
    }
    return { Arguments, trailingComma };
  }

  // #sec-class-definitions
  // ClassDeclaration :
  //   `class` BindingIdentifier ClassTail
  //   [+Default] `class` ClassTail
  //
  // ClassExpression :
  //   `class` BindingIdentifier? ClassTail
  parseClass(isExpression) {
    const node = this.startNode();

    this.expect(Token.CLASS);

    this.scope.with({ strict: true }, () => {
      if (!this.test(Token.LBRACE) && !this.test(Token.EXTENDS)) {
        node.BindingIdentifier = this.parseBindingIdentifier();
        if (!isExpression) {
          this.scope.declare(node.BindingIdentifier, 'lexical');
        }
      } else if (isExpression === false && !this.scope.isDefault()) {
        this.raise('ClassMissingBindingIdentifier');
      } else {
        node.BindingIdentifier = null;
      }
      node.ClassTail = this.scope.with({ default: false }, () => this.parseClassTail());
    });

    return this.finishNode(node, isExpression ? 'ClassExpression' : 'ClassDeclaration');
  }

  // ClassTail : ClassHeritage? `{` ClassBody? `}`
  // ClassHeritage : `extends` LeftHandSideExpression
  // ClassBody : ClassElementList
  parseClassTail() {
    const node = this.startNode();

    if (this.eat(Token.EXTENDS)) {
      node.ClassHeritage = this.parseLeftHandSideExpression();
    } else {
      node.ClassHeritage = null;
    }

    this.expect(Token.LBRACE);
    if (this.eat(Token.RBRACE)) {
      node.ClassBody = null;
    } else {
      this.scope.with({
        superCall: !!node.ClassHeritage,
        private: true,
      }, () => {
        node.ClassBody = [];
        let hasConstructor = false;
        while (this.eat(Token.SEMICOLON)) {
          // nothing
        }
        const staticPrivates = new Set();
        const instancePrivates = new Set();
        while (!this.eat(Token.RBRACE)) {
          const m = this.parseBracketedDefinition('class element');
          node.ClassBody.push(m);
          while (this.eat(Token.SEMICOLON)) {
            // nothing
          }

          if (m.ClassElementName?.type === 'PrivateIdentifier') {
            let type;
            if (m.type === 'FieldDefinition') {
              type = 'field';
            } else if (m.UniqueFormalParameters) {
              type = 'method';
            } else if (m.PropertySetParameterList) {
              type = 'set';
            } else {
              type = 'get';
            }
            if (type === 'get' || type === 'set') {
              if (m.static) {
                if (instancePrivates.has(m.ClassElementName.name)) {
                  this.raiseEarly('InvalidMethodName', m, m.ClassElementName.name);
                } else {
                  staticPrivates.add(m.ClassElementName.name);
                }
              } else {
                if (staticPrivates.has(m.ClassElementName.name)) {
                  this.raiseEarly('InvalidMethodName', m, m.ClassElementName.name);
                } else {
                  instancePrivates.add(m.ClassElementName.name);
                }
              }
            }
            this.scope.declare(m.ClassElementName, 'private', type);
            if (m.ClassElementName.name === 'constructor') {
              this.raiseEarly('InvalidMethodName', m, m.ClassElementName.name);
            }
          }

          const name = PropName(m);
          const isActualConstructor = !m.static
            && !!m.UniqueFormalParameters
            && m.type === 'MethodDefinition'
            && name === 'constructor';
          if (isActualConstructor) {
            if (hasConstructor) {
              this.raiseEarly('DuplicateConstructor', m);
            } else {
              hasConstructor = true;
            }
          }
          if ((m.static && name === 'prototype')
              || (!m.static && !isActualConstructor && name === 'constructor')) {
            this.raiseEarly('InvalidMethodName', m, name);
          }
          if (m.static && m.type === 'FieldDefinition' && name === 'constructor') {
            this.raiseEarly('InvalidMethodName', m, name);
          }
        }
      });
    }

    return this.finishNode(node, 'ClassTail');
  }

  parseClassExpression() {
    return this.parseClass(true);
  }

  parseTemplateLiteral(tagged = false) {
    const node = this.startNode();
    node.TemplateSpanList = [];
    node.ExpressionList = [];
    let buffer = '';
    while (true) {
      if (this.position >= this.source.length) {
        this.raise('UnterminatedTemplate', this.position);
      }
      const c = this.source[this.position];
      switch (c) {
        case '`':
          this.position += 1;
          node.TemplateSpanList.push(buffer);
          this.next();
          if (!tagged) {
            node.TemplateSpanList.forEach((s) => {
              if (TV(s) === undefined) {
                this.raise('InvalidTemplateEscape');
              }
            });
          }
          return this.finishNode(node, 'TemplateLiteral');
        case '$':
          this.position += 1;
          if (this.source[this.position] === '{') {
            this.position += 1;
            node.TemplateSpanList.push(buffer);
            buffer = '';
            this.next();
            node.ExpressionList.push(this.parseExpression());
            break;
          }
          buffer += c;
          break;
        default: {
          if (c === '\\') {
            buffer += c;
            this.position += 1;
          }
          const l = this.source[this.position];
          this.position += 1;
          if (isLineTerminator(l)) {
            if (l === '\r' && this.source[this.position] === '\n') {
              this.position += 1;
            }
            if (l === '\u{2028}' || l === '\u{2029}') {
              buffer += l;
            } else {
              buffer += '\n';
            }
            this.line += 1;
            this.columnOffset = this.position;
          } else {
            buffer += l;
          }
          break;
        }
      }
    }
  }

  // RegularExpressionLiteral :
  //   `/` RegularExpressionBody `/` RegularExpressionFlags
  parseRegularExpressionLiteral() {
    const node = this.startNode();
    this.scanRegularExpressionBody();
    node.RegularExpressionBody = this.scannedValue;
    this.scanRegularExpressionFlags();
    node.RegularExpressionFlags = this.scannedValue;
    try {
      const parse = (flags) => {
        const p = new RegExpParser(node.RegularExpressionBody);
        return p.scope(flags, () => p.parsePattern());
      };
      if (node.RegularExpressionFlags.includes('u')) {
        parse({ U: true, N: true });
      } else {
        const pattern = parse({ U: false, N: false });
        if (pattern.groupSpecifiers.size > 0) {
          parse({ U: false, N: true });
        }
      }
    } catch (e) {
      if (e instanceof SyntaxError) {
        this.raise('Raw', node.location.startIndex + e.position + 1, e.message);
      } else {
        throw e;
      }
    }
    const fakeToken = {
      endIndex: this.position - 1,
      line: this.line - 1,
      column: this.position - this.columnOffset,
    };
    this.next();
    this.currentToken = fakeToken;
    return this.finishNode(node, 'RegularExpressionLiteral');
  }

  // CoverParenthesizedExpressionAndArrowParameterList :
  //   `(` Expression `)`
  //   `(` Expression `,` `)`
  //   `(` `)`
  //   `(` `...` BindingIdentifier `)`
  //   `(` `...` BindingPattern `)`
  //   `(` Expression `,` `...` BindingIdentifier `)`
  //   `(` Expression `.` `...` BindingPattern `)`
  parseCoverParenthesizedExpressionAndArrowParameterList() {
    const node = this.startNode();
    const commaOp = this.startNode();
    this.expect(Token.LPAREN);
    if (this.test(Token.RPAREN)) {
      if (!this.testAhead(Token.ARROW) || this.peekAhead().hadLineTerminatorBefore) {
        this.unexpected();
      }
      this.next();
      node.Arguments = [];
      return this.finishNode(node, 'CoverParenthesizedExpressionAndArrowParameterList');
    }

    this.scope.pushArrowInfo();
    this.scope.pushAssignmentInfo('arrow');

    const expressions = [];
    let rparenAfterComma;
    while (true) {
      if (this.test(Token.ELLIPSIS)) {
        const inner = this.startNode();
        this.next();
        switch (this.peek().type) {
          case Token.LBRACE:
          case Token.LBRACK:
            inner.BindingPattern = this.parseBindingPattern();
            break;
          default:
            inner.BindingIdentifier = this.parseBindingIdentifier();
            break;
        }
        expressions.push(this.finishNode(inner, 'BindingRestElement'));
        this.expect(Token.RPAREN);
        break;
      }
      expressions.push(this.parseAssignmentExpression());
      if (this.eat(Token.COMMA)) {
        if (this.eat(Token.RPAREN)) {
          rparenAfterComma = this.currentToken;
          break;
        }
      } else {
        this.expect(Token.RPAREN);
        break;
      }
    }

    const arrowInfo = this.scope.popArrowInfo();
    const assignmentInfo = this.scope.popAssignmentInfo();

    // ArrowParameters :
    //   CoverParenthesizedExpressionAndArrowParameterList
    if (this.test(Token.ARROW) && !this.peek().hadLineTerminatorBefore) {
      node.Arguments = expressions;
      node.arrowInfo = arrowInfo;
      assignmentInfo.clear();
      return this.finishNode(node, 'CoverParenthesizedExpressionAndArrowParameterList');
    } else {
      this.scope.arrowInfo?.merge(arrowInfo);
    }

    // ParenthesizedExpression :
    //   `(` Expression `)`
    if (expressions[expressions.length - 1].type === 'BindingRestElement') {
      this.unexpected(expressions[expressions.length - 1]);
    }
    if (rparenAfterComma) {
      this.unexpected(rparenAfterComma);
    }
    if (expressions.length === 1) {
      node.Expression = expressions[0];
    } else {
      commaOp.ExpressionList = expressions;
      node.Expression = this.finishNode(commaOp, 'CommaOperator');
    }
    return this.finishNode(node, 'ParenthesizedExpression');
  }

  // PropertyName :
  //   LiteralPropertyName
  //   ComputedPropertyName
  // LiteralPropertyName :
  //   IdentifierName
  //   StringLiteral
  //   NumericLiteral
  // ComputedPropertyName :
  //   `[` AssignmentExpression `]`
  parsePropertyName() {
    if (this.test(Token.LBRACK)) {
      const node = this.startNode();
      this.next();
      node.ComputedPropertyName = this.parseAssignmentExpression();
      this.expect(Token.RBRACK);
      return this.finishNode(node, 'PropertyName');
    }
    if (this.test(Token.STRING)) {
      return this.parseStringLiteral();
    }
    if (this.test(Token.NUMBER) || this.test(Token.BIGINT)) {
      return this.parseNumericLiteral();
    }
    return this.parseIdentifierName();
  }

  // ClassElementName :
  //   PropertyName
  //   PrivateIdentifier
  parseClassElementName() {
    if (this.test(Token.PRIVATE_IDENTIFIER)) {
      return this.parsePrivateIdentifier();
    }
    return this.parsePropertyName();
  }

  // PropertyDefinition :
  //   IdentifierReference
  //   CoverInitializedName
  //   PropertyName `:` AssignmentExpression
  //   MethodDefinition
  //   `...` AssignmentExpression
  // MethodDefinition :
  //   ClassElementName `(` UniqueFormalParameters `)` `{` FunctionBody `}`
  //   GeneratorMethod
  //   AsyncMethod
  //   AsyncGeneratorMethod
  //   `get` ClassElementName `(` `)` `{` FunctionBody `}`
  //   `set` ClassElementName `(` PropertySetParameterList `)` `{` FunctionBody `}`
  // GeneratorMethod :
  //   `*` ClassElementName `(` UniqueFormalParameters `)` `{` GeneratorBody `}`
  // AsyncMethod :
  //   `async` [no LineTerminator here] ClassElementName `(` UniqueFormalParameters `)` `{` AsyncFunctionBody `}`
  // AsyncGeneratorMethod :
  //   `async` [no LineTerminator here] `*` ClassElementName `(` UniqueFormalParameters `)` `{` AsyncGeneratorBody `}`
  parseBracketedDefinition(type) {
    const node = this.startNode();

    if (type === 'property' && this.eat(Token.ELLIPSIS)) {
      node.PropertyName = null;
      node.AssignmentExpression = this.parseAssignmentExpression();
      return this.finishNode(node, 'PropertyDefinition');
    }

    let firstFirstName;
    if (type === 'class element') {
      if (this.test('static') && (
        this.testAhead(Token.ASSIGN)
        || this.testAhead(Token.SEMICOLON)
        || this.peekAhead().hadLineTerminatorBefore
        || isAutomaticSemicolon(this.peekAhead().type)
      )) {
        node.static = false;
        firstFirstName = this.parseIdentifierName();
      } else {
        node.static = this.eat('static');
        this.markNodeStart(node);
      }
    }

    let isGenerator = this.eat(Token.MUL);
    let isGetter = false;
    let isSetter = false;
    let isAsync = false;
    if (!isGenerator) {
      if (this.test('get')) {
        isGetter = true;
      } else if (this.test('set')) {
        isSetter = true;
      } else if (this.test('async') && !this.peekAhead().hadLineTerminatorBefore) {
        isAsync = true;
      }
    }

    const firstName = firstFirstName || (type === 'property'
      ? this.parsePropertyName()
      : this.parseClassElementName());

    if (!isGenerator && isAsync) {
      isGenerator = this.eat(Token.MUL);
    }

    const isSpecialMethod = isGenerator || ((isSetter || isGetter || isAsync) && !this.test(Token.LPAREN));

    if (!isGenerator) {
      if (type === 'property' && this.eat(Token.COLON)) {
        node.PropertyName = firstName;
        node.AssignmentExpression = this.parseAssignmentExpression();
        return this.finishNode(node, 'PropertyDefinition');
      }

      if (type === 'class element' && (
        this.test(Token.ASSIGN)
        || this.test(Token.SEMICOLON)
        || this.peek().hadLineTerminatorBefore
        || isAutomaticSemicolon(this.peek().type)
      )) {
        node.ClassElementName = firstName;
        node.Initializer = this.scope.with({ superProperty: true }, () => this.parseInitializerOpt());
        const argumentNode = node.Initializer && ContainsArguments(node.Initializer);
        if (argumentNode) {
          this.raiseEarly('UnexpectedToken', argumentNode);
        }
        this.finishNode(node, 'FieldDefinition');
        this.semicolon();
        return node;
      }

      if (type === 'property' && this.scope.assignmentInfoStack.length > 0 && this.test(Token.ASSIGN)) {
        node.IdentifierReference = firstName;
        node.IdentifierReference.type = 'IdentifierReference';
        node.Initializer = this.parseInitializerOpt();
        this.finishNode(node, 'CoverInitializedName');
        this.scope.registerObjectLiteralEarlyError(this.raiseEarly('UnexpectedToken', node));
        return node;
      }

      if (type === 'property'
          && !isSpecialMethod
          && firstName.type === 'IdentifierName'
          && !this.test(Token.LPAREN)
          && !isKeyword(firstName.name)) {
        firstName.type = 'IdentifierReference';
        this.validateIdentifierReference(firstName.name, firstName);
        return firstName;
      }
    }

    if (isSpecialMethod && (!isGenerator || isAsync)) {
      if (type === 'property') {
        node.ClassElementName = this.parsePropertyName();
      } else {
        node.ClassElementName = this.parseClassElementName();
      }
    } else {
      node.ClassElementName = firstName;
    }

    this.scope.with({
      lexical: true,
      variable: true,
      superProperty: true,
      await: isAsync,
      yield: isGenerator,
    }, () => {
      if (isSpecialMethod && isGetter) {
        this.expect(Token.LPAREN);
        this.expect(Token.RPAREN);
        node.PropertySetParameterList = null;
        node.UniqueFormalParameters = null;
      } else if (isSpecialMethod && isSetter) {
        this.expect(Token.LPAREN);
        node.PropertySetParameterList = [this.parseFormalParameter()];
        this.expect(Token.RPAREN);
        node.UniqueFormalParameters = null;
      } else {
        node.PropertySetParameterList = null;
        node.UniqueFormalParameters = this.parseUniqueFormalParameters();
      }

      this.scope.with({
        superCall: !isSpecialMethod
                   && !node.static
                   && (node.ClassElementName.name === 'constructor' || node.ClassElementName.value === 'constructor')
                   && this.scope.hasSuperCall(),
      }, () => {
        const body = this.parseFunctionBody(isAsync, isGenerator, false);
        node[`${isAsync ? 'Async' : ''}${isGenerator ? 'Generator' : 'Function'}Body`] = body;
        if (node.UniqueFormalParameters || node.PropertySetParameterList) {
          this.validateFormalParameters(node.UniqueFormalParameters || node.PropertySetParameterList, body, true);
        }
      });
    });

    const name = `${isAsync ? 'Async' : ''}${isGenerator ? 'Generator' : ''}Method${isAsync || isGenerator ? '' : 'Definition'}`;
    return this.finishNode(node, name);
  }
}
